import React from "react";
import profile from "./profile.jpg";
const About = () => {
  return (
    <section id="about">
      <div className="container">
        <div className="row">
          <div className="col-12">
            <h3 id="about">About Us</h3>
            <hr />
          </div>
        </div>
        <div className="row ">
          <div className="col-12 col-md-6">
            <h2>Subhash Parwani</h2>
            <p>
              I am an electrical engineer. I am also full-stack web developer. I
              have two years of experience in creating the websites. Currently,
              I am founder and CEO at Youth-Talks (www.youth-talks.com). An
              e-magazine for writing professional articles.
            </p>
            <p>
              Youth-Talks is a firm that specialises in media, publishing,
              consulting and production. Ongoing coverage and insight on all
              elements of youth culture as seen through the perspective of
              meaningful change are provided by our staff. We're cultivating a
              community of young journalists and writers and providing them with
              a forum to speak out about critical problems.
            </p>
          </div>
          <div className="col-12 col-md-5">
            <img className="img-fluid" src={profile} alt="profile" />
          </div>
        </div>
      </div>
    </section>
  );
};

export default About;
