import React from "react";
import one from "./1.png";
import two from "./2.png";
import three from "./3.png";

const Services = () => {
  return (
    <div className="container">
      <div className="row">
        <div className="col-12">
          <h3 id="services">Services</h3>
          <hr />
        </div>
      </div>
      <div className="row">
        <div className="col-sm">
          <div class="card">
            <img class="card-img-top" src={one} />
            <div class="card-body">
              <h5 class="card-title">E-Magazine (youth-talks.com)</h5>
              <p class="card-text">
                Youth-Talks is an e-magazine. I have created this platform to
                give an opportunity to young writers to write the professional
                articles.
              </p>
            </div>
          </div>
        </div>
        <div className="col-sm">
          <div class="card">
            <img class="card-img-top" src={two} alt="Card image cap" />
            <div class="card-body">
              <h5 class="card-title">Restaurant Website</h5>
              <p class="card-text">
                I can create any restaurant website where the customer can book
                the table or the person can order using portal.
              </p>
            </div>
          </div>
        </div>
        <div className="col-sm">
          <div class="card">
            <img class="card-img-top" src={three} alt="Card image cap" />
            <div class="card-body">
              <h5 class="card-title">Portfolio Website</h5>
              <p class="card-text">
                I can create any portfolio website where you can enlist your
                services or projects. It can help you to get the job.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Services;
